let cartItems = [];

/*---------------------------------------------------------------------------------*/
/*  Affiche nombre de produit au chargement de la page
/*---------------------------------------------------------------------------------*/

    $(window).on('load', function () {
        var nbProduitPanier = cartItems.length;
        $('#nbProduitPanier').html(nbProduitPanier);
    });

/*---------------------------------------------------------------------------------*/
/*  Ajoute produit panier
/*---------------------------------------------------------------------------------*/
$( ".modal-placement" ).on( "click", ".ajout-panier",
    function addProductInCart(productId) {
    var productId = $(this).attr("data-id");
    cartItems.push(productId);
    let ls_cart = cartItems.join(',');
    window.localStorage.setItem('ls_cart', ls_cart); // Ajoute dans localStorage
    $('#messageAjout').html("✓ L'article a bien été ajouté à votre panier.");
    // MAJ PANIER NOMBRE PRODUIT
    var nbProduitPanier = cartItems.length;
    $('#nbProduitPanier').html(nbProduitPanier);
});





/*---------------------------------------------------------------------------------*/
/*  Recup panier depuis localstorage
/*---------------------------------------------------------------------------------*/
document.addEventListener('DOMContentLoaded', ()=>{ 
    let ls_cart = window.localStorage.getItem('ls_cart');
    if  (ls_cart) {
    cartItems = ls_cart.split(',');
    } else {
    // window.localStorage.setItem('ls_cart', '15,125,6');
    }
});
    



