/*---------------------------------------------------------------------------------*/
/*  MODALE PANIER
/*---------------------------------------------------------------------------------*/

var modal = document.getElementById("myModalPanier");

/*---------------------------------------------------------------------------------*/
/*  affiche modale
/*---------------------------------------------------------------------------------*/

$( ".btn-panier" ).click(function(e) {
    e.preventDefault();
    $('#myModalPanier').css('display', 'block')
        $.ajax({
            url: ajaxurl,
            type: 'POST',
            data: {
                'action': 'affiche_details_panier',
                'param': 'cartItems='+ cartItems,
            }
            }).done(function(response) {
                // $('.button_container').css('display', "none"); // cache le burger menu
                $('.modal-placement-panier').html(response);
                
                // return false;
            })
            .fail(function () { 
                console.log("Impossible d'afficher le panier.");
                // return false;
            });
});


/*---------------------------------------------------------------------------------*/
/*  Au chargement, cache la modale
/*---------------------------------------------------------------------------------*/
    $(window).on('load', function () {
        $('#myModalPanier').css('display', 'none')
    });

/*---------------------------------------------------------------------------------*/
/*  Fermeture modale
/*---------------------------------------------------------------------------------*/
    $('.close-panier').click(function() {
        $('#myModalPanier').css('display', 'none');
        $( ".modal-placement" ).empty();
        // $('.button_container').css('display', 'block'); // affiche le burger menu
        })

    //  Quand l'utlisateur click en dehors de la modale, la ferme
    $(window).click (function(event) {
        if (event.target == modal) {
            $('#myModalPanier').css('display', 'none');
            $( ".modal-placement" ).empty();
            // $('.button_container').css('display', 'block'); // affiche le burger menu
        }
    });


/*---------------------------------------------------------------------------------*/
/*  FORMULAIRE COMMANDE
/*---------------------------------------------------------------------------------*/

$( ".modal-placement-panier" ).on( "submit", "#formcommande", function(e) {
    e.preventDefault();

    {
        jQuery('#send-message').toggle();
     };
     $('#texteEnvoi').append('Envoi de la commande en cours...');

    var error = false;
    var form = $('#formcommande');
    var commandeClient = $(".panier-article");
    let ls_cart = window.localStorage.getItem('ls_cart');

    // boucle formulaire, vérifie que les champs requis sont remplis
    form.find('[required]').each (function() {
        if( $.trim( $(this).val() ) == '' )  {
            $(this).addClass('border-danger');
            error = true;
        } else {
            $(this).addClass('border-secondary');
        }
    });


    // Si tous les champs sont bien remplis, envoie les données avec AJAX
    if( !error) {

        $.ajax({
            url: ajaxurl,
            type: 'POST',
            data: {
                action: 'send_form_command',
                security: $('#security').val(),
                param: form.serialize() + "&cart=" + ls_cart,
            }
        }).done(function() {
            localStorage.clear();
            $('.modal-left').css('display', 'none');
            $('.modal-right').css('display', 'none');
            $('.modal-placement-panier').replaceWith( 'Merci, votre commande a bien été envoyée !' + '<br/>' + 'Vous serez bientôt recontacté.');
            $('.button_container').css('display', "none"); // cache le burger menu
            $('#myModalPanier').delay(5000).fadeOut();
            setTimeout(function(){ location.reload(); }, 5000);
        })
        .fail(function () { 
            alert("Nous sommes désolés, votre commande n'a pas pu être envoyée. Si le problème persiste, merci de prendre contact avec nous via la section 'contact'.")
         });
}})


