/*---------------------------------------------------------------------------------*/
/*  PAGINATION
/*---------------------------------------------------------------------------------*/
var current_width = $(window).width();


$(document).ready(function(){
    if ((current_width >= 848 && current_width < 1114)) {
        $("#tab").pagination({
            items: 9,
            contents: 'liste-produits',
            previous: '<<',
            next: '>>',
            position: 'bottom',
            })
    } else {
        $("#tab").pagination({
            items: 8,
            contents: 'liste-produits',
            previous: '<<',
            next: '>>',
            position: 'bottom',
            })
    }
});


